/* Writing.css */

#writing {
  padding: 20px;
  background-color: #ccc;
}

.writing-container {
  display: flex;
  flex-wrap: wrap;

}

.paper-group {
  width: 50%; /* Each paper group takes up half of the container width */
  display: flex;
  margin-bottom: 20px; /* Add some space between paper groups */
  border-top: #111111 solid 2px;
  border-bottom: #111111 solid 2px;
}

.paper-info {
  flex: 1;
  padding: 10px;
}

.paper-image {
  flex: 1;
  display: flex;
  justify-content: center; /* Center the image horizontally */
  align-items: center; /* Center the image vertically */
}

.paper-image img {
  max-height: 180px;
  max-width: 300px;
  width: auto;
}

.writing-container button {
  background-color: #3498db; /* Button background color */
  color: #fff; /* Button text color */
  padding: 10px 20px; /* Button padding */
  border: none; /* Remove button border */
  border-radius: 5px; /* Add border radius */
  margin: 10px 0; /* Add margin between buttons */
  cursor: pointer; /* Change cursor to pointer on hover */
  transition: background-color 0.3s ease; /* Add transition effect */
}

.writing-container button:hover {
  background-color: #2980b9; /* Darken button color on hover */
}

.close-button {
  background-color: red; /* Button background color */
  color: #fff; /* Button text color */
  padding: 10px 20px; /* Button padding */
  border: none; /* Remove button border */
  border-radius: 5px; /* Add border radius */
  margin-top: 10px; /* Add margin between button and content */
  cursor: pointer; /* Change cursor to pointer on hover */
  transition: background-color 0.3s ease; /* Add transition effect */
  position: absolute; /* Position the button */
  top: 10px; /* Distance from the top */
  right: 10px; /* Distance from the right */
}

.close-button:hover {
  background-color: darkred; /* Darken button color on hover */
}

.pdf-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black overlay */
  display: flex;
  justify-content: center;
  align-items: center;
}

.pdf-popup {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3); /* Add a subtle shadow */
}
